@use 'variables' as *;

.sticky {
	position: fixed !important;
	top: 2rem;
}
#navbar {
	display: flex;
	justify-content: right;
	align-items: center;
	z-index: 10;
	height: 2.5rem;
	color: $darkerBlue;
	position: fixed;
	top: 2rem;
	right: 0;
}
#menuBurger {
	margin-right: 2rem;
	z-index: 10;
}
#menuWord {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	margin-right: 2rem;
	cursor: pointer;
	z-index: 5;
	color: $darkerBlue;
	display: none;
	h4 {
		color: $darkerBlue;
		text-shadow: 1px 1px 0 $lighterBlue;
		font-size: 1.1rem;
		display: inline-block;
		transition: 0.4s;
		width: 100%;
		font-size: 2rem;
		text-align: center;
		padding-left: 2rem;
	}
	.bar {
		width: 0;
	}
	.bar2 {
		opacity: 0;
	}
}
.bar {
	width: 35px;
	height: 6px;
	background-color: $white;
	margin: 5px 0;
	transition: 0.4s;
	border-radius: 2px;
	box-sizing: border-box;
	box-shadow: 1px 1px 0 black;
}
.change {
	.bar1 {
		transform: translate(0, 11px) rotate(-45deg);
	}
	.bar2 {
		opacity: 0;
	}
	.bar3 {
		transform: translate(0, -11px) rotate(45deg);
	}
	.menuItem {
		font-size: 2rem;
	}
	.menuOptions {
		padding: 1rem;
	}
}
#menuWord.change {
	.bar1 {
		width: 35px;
		justify-self: center;
		border: 1px solid $softBlack;
	}
	.bar2 {
		width: 35px;
		justify-self: center;
		border: 1px solid $softBlack;
	}
	.bar3 {
		width: 35px;
		justify-self: center;
		border: 1px solid $softBlack;
	}
	h4 {
		width: 0;
		font-size: 0rem;
	}
}
#navBg {
	width: 100%;
	height: 0;
	transition: 0.4s;
	position: absolute;
	z-index: 2;
	top: -2rem;
}
.stickyBg {
	height: 6rem !important;
	padding-left: 2rem;
}
.theMenu {
	z-index: 3;
	display: block;
	height: 0;
	width: 0;
	align-self: flex-start;
	font-size: 0;
	transition: 0.4s;
	position: relative;
}
.change.theMenu {
	width: 14rem;
	height: 0;
}
.menuOptions {
	background-color: $softBlackAlpha;
	padding: 0;
}
.menuItem {
	margin-top: 0.5rem;
	font-size: 0;
	list-style: none;
}
.menuLink {
	color: $white;
	text-decoration: none;
}